import React from "react";
import { useHistory } from "react-router-dom";

import virus1 from "../img/mockup1.png";
import virus2 from "../img/mockup2.png";
import lofi1 from "../img/viruslofi1.png";
import lofi2 from "../img/viruslofi3.png";
import lofi3 from "../img/viruslofi4.png";
import arcvirus from "../img/arcvitus.png";
import Fade from "react-reveal/Fade";

const Project2 = () => {
  const history = useHistory();

  window.onscroll = function () {
    scrollFunction();
  };
  function linkClick() {
    let width = window.innerWidth;
    var li = document.getElementById("link");
    var lis = document.getElementById("links");
    if (width < 769) {
      li.style.width = "100%";

      lis.style.display = "block";
    } else {
      if (document.getElementById("link") !== "null") {
        console.log(li.style.width);
        if (li.style.width === "90%") {
          li.style.width = "0.3vw";
          li.style.height = "1vw";
          lis.style.display = "none";
        } else if (li.style.width === "0.3vw") {
          li.style.width = "90%";
          lis.style.display = "block";
        }
      }
    }
  }

  function scrollFunction() {
    let width = window.innerWidth;
    var li = document.getElementById("link");
    var lis = document.getElementById("links");
    if (width < 769) {
      li.style.width = "100%";

      lis.style.display = "block";
    } else {
      if (
        document.body.scrollTop > 50 ||
        document.documentElement.scrollTop > 50
      ) {
        li.style.width = "0.3vw";

        li.style.height = "1vw";
        lis.style.display = "none";
        li.onclick = linkClick;
      } else {
        li.style.width = "90%";
        lis.style.display = "block";
        li.onclick = linkClick;
      }
    }
  }

  function handleClickProject() {
    history.push("/aliceljungholm/Project");
  }

  function handleClickAbout() {
    history.push("/aliceljungholm/about");
  }
  function handleHomeClick() {
    history.push("/aliceljungholm");
  }

  return (
    <div className="pro">
      <div id="link">
        <Fade left>
          <div id="links">
            <button id="menuLink" onClick={handleHomeClick}>
              Home
            </button>{" "}
            |{" "}
            <button id="menuLink" onClick={handleClickProject}>
              Projects
            </button>{" "}
            |{" "}
            <button id="menuLink" onClick={handleClickAbout}>
              About
            </button>{" "}
            |{" "}
            <button id="menuLink" href="#contact-me">
              Contact
            </button>
          </div>
        </Fade>
      </div>
      <div id="swave"></div>

      <h1 className="header1">COMBAT COVID</h1>

      <Fade bottom>
        <div className="cont">
          <div id="thesisText">
            <div id="sumrole">
              <div>
                <h3>Summary</h3>
                <p className="leftText">
                  Combat Covid on Campus (CCC) was created by me and two other
                  students. The app took inspiration from “Popular Times” on
                  Google and tackled the question “How to Combat Covid on
                  Campus”. CCC allowed users to check-in to campus before going,
                  letting the people who wanted to avoid crowds know when campus
                  was busy. The app also allowed for potential contract tracing
                  for the COVID-19 virus. CCC was designed in Figma and later
                  implemented using React.
                </p>
              </div>
              <div id="rightText">
                <h3>Role</h3>
                <ul>
                  <li>UX-designer</li>
                  <li>Front-End Developer</li>
                  <li>User Tester</li>
                </ul>
              </div>
            </div>
            <h3>The design idea</h3>
            <p className="leftText">
              {/*Testing was made on 7 people per iteration, since Normans
              citation. The testing was task-based.*/}{" "}
              <ul>
                <li>Covid Safe</li>
                <li>Easy to use</li>
                <li>Encourage responsibility</li>
              </ul>
            </p>
            <h3>The arcitecture</h3>
            <p id="left">
              We took the key points we got and mapped out a architecture for
              the application.{" "}
            </p>
            <img alt="architecture" id="arc" src={arcvirus}></img>
            <p id="left">
              This arcitecture is the main flow of the application, and is, as
              seen in the picture, straight-forward and shallow.
            </p>
            <h3>The Design Stages</h3>
            <h4>LoFi</h4>
            <p id="left">
              The design started on paper, which quickly translated to Figma,
              when we were required to work remotely.{" "}
            </p>
            <div id="lofitrio">
              <img alt="lofi" src={lofi1}></img>
              <img alt="Mockup" src={lofi2}></img>
              <img alt="Mockup" src={lofi3}></img>
            </div>
            <h4>Testing</h4>
            We tested on 5 people per iteration, since Nielsen Norman Group
            recommend that number, and with the time frame, we did not have
            time.
            <h5>Result from testing</h5>
            <p id="left">
              The testing resulted in some few design changes. One big finding
              is that the onboarding is necessary since the purpose of the
              application needs to be explained.
            </p>
          </div>

          <h4>Final Design</h4>
          <div id="virustrio">
            <img alt="Mockup" src={virus1}></img>
            <img alt="Mockup" src={virus2}></img>
          </div>

          <div id="skills">
            <Fade left>
              <div id="skillcontent">
                <div id="skill">
                  <h3>Skills</h3>
                  <li>Clean Code</li>
                  <li>UX/UI</li>
                  <li>Wireframing</li>
                  <li>Rapid Prototyping</li>
                  <li>User Research</li>
                  <li>User Testing</li>
                  <li>Mobile</li>
                </div>
                <div id="skill">
                  <h3>Tools</h3>
                  <ul>
                    <li>Figma</li>
                    <li>Javascript</li>
                    <li>React</li>
                    <li>GitHub/GitLab</li>
                  </ul>
                </div>
              </div>
            </Fade>
          </div>
        </div>
        <div id="backH">
          <button id="BackHome" onClick={handleClickProject}>
            Back to all Projects
          </button>
        </div>
      </Fade>
    </div>
  );
};
export default Project2;
